import { Directive, PipeTransform } from '@angular/core';

@Directive({
  selector: '[appMapFromKeyValuePipe]'
})
export class MapFromKeyValuePipeDirective implements PipeTransform {
  transform(objArr) {
    console.log('obj array looks like this: ', objArr);
    let mappingObject = {};
    objArr.forEach((obj) => {
      for (let key in obj) {
        let value = obj[key];
        mappingObject[key] = value;
      }
    })
    return mappingObject;
  }
}
